import KatalogRestoranSource from '../../data/katalogRestoranSource'
import { createCardItemTemplate } from '../templates/template-creator'

const Katalog = {
  async render() {
    return `
      <picture class="hero-img">
          <source
            media="(max-width: 600px)"
            srcset="./hero/hero-image_2-small.jpg"
          />
          <img
            src="./hero/hero-image_2.jpg"
            alt="Gambar Hero"
          />
      </picture>
      </div>
      <h2>Daftar Restoran</h2>
      <div class="restaurant-list"></div>
    `
  },

  async afterRender() {
    const restaurantListElement = document.querySelector('.restaurant-list')
    const katalog = await KatalogRestoranSource.katalog()

    katalog.forEach((restoran) => {
      restaurantListElement.innerHTML += createCardItemTemplate(restoran)
    })
  }
}

export default Katalog
