import FavoritePlaceIdb from '../../data/favorite-place-idb'
import { createCardItemTemplate } from '../templates/template-creator'

const Favorite = {
  async render() {
    return `
      <h2>Restoran Favorit</h2>
      <div class="restaurant-list"></div>
    `
  },

  async afterRender() {
    const moviesContainer = document.querySelector('.restaurant-list')
    const places = await FavoritePlaceIdb.getAllPlaces()

    places.forEach((place) => {
      moviesContainer.innerHTML += createCardItemTemplate(place)
    })
  }
}

export default Favorite
