import UrlParser from '../../routes/url-parser'
import KatalogRestoranSource from '../../data/katalogRestoranSource'
import { createCardDetailTemplate } from '../templates/template-creator'
import LikeButtonPresenter from '../../utils/like-button-presenter'
import pushDataReview from '../../data/push-data-review'

const Detail = {
  async render() {
    return `
      <div class="content"></div>
      <div id="likeButtonContainer"></div>
    `
  },

  async afterRender() {
    const movieDetailContainer = document.querySelector('.content')
    const url = UrlParser.parseActiveUrlWithoutCombiner()
    const data = await KatalogRestoranSource.detailRestoran(url.id)
    const place = data.restaurant
    movieDetailContainer.innerHTML = createCardDetailTemplate(place)

    LikeButtonPresenter.init({
      likeButtonContainer: document.querySelector('#likeButtonContainer'),
      place: {
        id: place.id,
        name: place.name,
        description: place.description,
        pictureId: place.pictureId,
        city: place.city,
        rating: place.rating
      }
    })

    const inputForm = document.querySelector('.input_form')

    inputForm.onsubmit = (event) => {
      event.preventDefault()
      pushDataReview(url.id)
      movieDetailContainer.innerHTML = ''
      movieDetailContainer.innerHTML = createCardDetailTemplate(place)
    }
  }
}

export default Detail
